//Search Icon 
let overlay = document.querySelector(".overlay");
let searchIcon = document.querySelector("a.search");
let searchBox = document.querySelector(".search-box .container");
let closeBtn = document.querySelector(".search-box .container span.close");
searchIcon.onclick = (e) => {
    e.preventDefault();
    overlay.classList.add("active");
    searchBox.classList.add("active");
    searchBox.children[1].focus();
}

closeBtn.onclick = () => {
    searchBox.classList.remove("active");
    overlay.classList.remove("active");
}
//Mobile Menu
let burgerIcon = document.querySelector(".burger-icon");
let links = document.querySelectorAll("ul.links");
let productsMenu = document.querySelector("a.prods-menu");
let products = document.querySelector("a.prods-menu +.products-menu .products");
let specials = document.querySelector("a.spec-menu");
let specialsMenu = document.querySelector("a.spec-menu + .specials");
burgerIcon.onclick = (e) => {
    // e.targ   et.stopProopagation();
    links.forEach((el) => {
        el.classList.toggle("active");
    })
}
productsMenu.onclick = (e) => {
    e.preventDefault();
    if (products.classList.contains("active")) {
        products.classList.remove("active");
        links[1].classList.remove("toggled");
    } else {
        products.classList.add("active");
        links[1].classList.add("toggled");
    }

}
specials.onclick = (e) => {
    e.preventDefault();
    if (specialsMenu.classList.contains("active")) {
        specialsMenu.classList.remove("active");
    } else {
        specialsMenu.classList.add("active");
    }
}