//Categories Page
let filterBtn = document.querySelector(".cat-page-content .wrapper >i.filter-trigger");
let filterMenu = document.querySelector(".cat-page-content .sidebar");
let closeFilter = document.querySelector(".cat-page-content .sidebar >i.filter-close");
if (filterBtn) {
    filterBtn.onclick = () => {
        filterMenu.classList.add("active");
    }
}
if (closeFilter) {
    closeFilter.onclick = () => {
        filterMenu.classList.remove("active");
    }
}