//Product Thumb Images Swiper
const thumbsSwiber = new Swiper('.thumb-imgs', {
    // Optional parameters

    direction: 'vertical',
    spaceBetween: 15,
    slidesPerView: 1,
    freeMode: true,
    watchSlidesProgrees: true,

});
//Product Main Images Swiper
const mainImgSwiber = new Swiper('.main-image', {
    // Optional parameters


    loop: true,
    autoHeight: true,
    spaceBetween: 15,

    thumbs: {
        swiper: thumbsSwiber,
    }


});