//Categories
let catTitleList = document.querySelector(".selected-category");
let catList = document.querySelector("ul.categories-list");
let catItems = document.querySelectorAll("ul.categories-list >li");
let catTitle = document.querySelector(".selected-category>h3.title");
let category = document.querySelectorAll(".category");
if (catTitleList) {
    catTitleList.onclick = () => {
        catList.classList.toggle("active");
    }
}



catItems.forEach((item) => {
    item.addEventListener("click", (e) => {
        if (e.currentTarget.classList.contains("active")) {
            return null;
        }
        else {
            catItems.forEach((el) => {
                el.classList.remove("active");
            });
            e.currentTarget.classList.add("active");
            e.currentTarget.parentElement.classList.remove("active");
            catTitle.textContent = e.currentTarget.textContent;
            category.forEach((cat) => {
                if (cat.classList.contains("active")) {
                    cat.classList.remove("active");
                }

                if (cat.classList.contains(e.currentTarget.textContent.toLowerCase())) {
                    cat.classList.add("active");
                }

            })
        }
    })
})