//Select Elements
let productAboutList = document.querySelectorAll(".product-about .container >ul >li a");
let contents = document.querySelectorAll(".product-about .content");
productAboutList.forEach(item => {

    item.addEventListener("click", e => {
        e.preventDefault();
        productAboutList.forEach(el => {
            el.classList.contains("active") ? el.classList.remove("active") : null;
        });
        item.classList.add("active");
        contents.forEach(content => {
            content.classList.contains("active") ? content.classList.remove("active") : null;

        })
        contents.forEach(c => {
            item.dataset.id === c.id ? c.classList.add("active") : null;
        })
    })
})