//Share Popup
let popBtns = document.querySelectorAll(".pop-btn");
let popups = document.querySelectorAll(".popup");
let closePops = document.querySelectorAll(".popup .pop-close");

function showHidePopup(popBtns, popups, closeBtn) {
    popBtns.forEach(btn => {
        btn.onclick = e => {
            e.preventDefault();
            overlay.classList.add("active");
            popups.forEach(popup => {

                popup.classList.contains(btn.id) ? popup.classList.add("active") : null;
            })
        }
    });
    closePops.forEach(clPop => {
        clPop.onclick = _ => {
            clPop.parentElement.classList.remove("active");
            overlay.classList.remove("active");
        }
    })
}
showHidePopup(popBtns, popups, closePops);
