const arrivalsSwiper = new Swiper('.arrivals-swiper', {

    slidesPerView: 'auto',
    centeredSlides: true,
    spaceBetween: 30,
    // Navigation arrows
    navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
    },
    breakpoints: {
        575: {
            slidesPerView: 2,
            slidesPerGroup: 1,
            centeredSlides: false,
        },
        768: {
            slidesPerView: 3,
            slidesPerGroup: 3,
            centeredSlides: false,
        },
        992: {
            slidesPerView: 4,
            slidesPerGroup: 4,
            centeredSlides: false,
        },

    }
});